import React from 'react'
import styled from 'styled-components'
import '../assets/css/0_basic.scss'
import sohyun from '../assets/img/exhibit_main/sohyun.jpg'
import hasom from '../assets/img/exhibit_main/hasom.jpg'
import cheolhan from '../assets/img/exhibit_main/cheolhan.jpg'

    const Container = styled.div`
        display: grid;
        grid-template-columns: 309px 310px 309px;
        column-gap: 24px;
    `

    const Button = styled.button`
        text-align: center;
        background-color: #efafed;
        border: 1px solid #ffffff;
        margin-top: 12px;
    `;

    const Box = styled.div`
        display: flex;
        flex-direction: column;
        align-items: center;
    `;

    const Img = styled.img`
        width: 100%;
        height: 400px;
        object-fit: cover;
    `;

function Picture() {

    return (
        <>
            <Container>
                <Box>
                    <Img src={sohyun}></Img>
                    <Button>First Color_Blue 
                    <br/>
                    <br/>배 소 현 전시</Button>
                </Box>
                <Box>
                <Img src={hasom}></Img>        
                    <Button>Second Color_Yellow
                    <br/>
                    <br/>오 하 솜 전시</Button>
                </Box> 
                <Box>
                    <Img src={cheolhan} ></Img>
                    <Button>Third Color_Gray
                    <br/>
                    <br/>정 철 한 전시</Button>
                </Box>
            </Container>
        </>
    )
}

export default Picture;