import React from 'react'
import Introduction from './2_1_Introduction';
import Content from './2_2_Text';
import Picture from './2_3_Picture';
import Reservation from './2_4_Reservation';
import Content1 from './2_5_Text';
import Content2 from './2_6_Text';
import Content3 from './2_7_Text';
import Content4 from './2_8_Text';
import Human_Exhibition from '../assets/img/Human_Exhibition.png';
import '../assets/css/human_main/2_0_main.scss';
import styled from 'styled-components';
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block", background: "red" }}
            onClick={onClick}
        />
    );
}

function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block", background: "green" }}
            onClick={onClick}
        />
    );
}

    const Img = styled.img`
        display: flex;
        width: 100%;
        object-fit: cover;
    `;

function Main() {

    const StyledSlider = styled(Slider)`
    width: 100vw;
    height: 90vh;
    `

    const settings = {
        dots: true,
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        vertical: true,
        dotsClass : "slick-dots",
        draggable : false,
        swipe : true,
    };

    return ( 
        <main>
            {/* <StyledSlider {...settings}> */}
                <div className='item'>
                    <Img src={Human_Exhibition} alt=""/>
                    <div className='item_box'><Introduction></Introduction></div>
                </div>
                <div className='item'><div className='item_box'><Content></Content></div></div>
                <div className='item'><div className='item_box'><Picture></Picture></div></div>
                <div className='item'><div className='item_box'><Reservation></Reservation></div></div>
                <div className='item'><div className='item_box'><Content1></Content1></div></div>
                <div className='item'><div className='item_box'><Content2></Content2></div></div>
                <div className='item'><div className='item_box'><Content3></Content3></div></div>
                <div className='item'><div className='item_box'><Content4></Content4></div></div>
                <div className='item'><div className='item_box'></div></div>
                <div className='item'><div className='item_box'></div></div>
                <div className='item'><div className='item_box'></div></div>
            {/* </StyledSlider> */}
        </main> 
    );
}

export default Main;