import React, { useState } from 'react'
import styled from 'styled-components'
import SelectExhibit from './1_SelectExhibit';
import SelectDay from './2_SelectDay';
import SelectTime from './3_SelectTime';
import '../assets/css/0_basic.scss'

    const Div = styled.div`
        display: flex;
        flex-direction: column;
        align-items: center;
    `

    const Button = styled.button`
        width: 328px;
        height: 56px;
        background: #DBFD5C;
        box-shadow: 0px -1px 4px rgba(255, 255, 255, 0.12), 0px 2px 16px rgba(0, 0, 0, 0.24);
        backdrop-filter: blur(4px);
        border-radius: 12px;
        margin-top 12px;
        font-size: 16px;
        line-height: 22px;
        color: black;
    `;

function Reservation() {

    const [exhibit, setExhibit] = useState('전시를 선택하세요')
    const [day, setDay] = useState('날짜를 선택하세요')
    const exhibit_day = [
        {exhibit: '전시를 선택하세요'},
        {exhibit: '전시1', first:'9월 3일', second:'9월 4일'},
        {exhibit: '전시2', first:'9월 17일', second:'9월 18일'},
        {exhibit: '전시3', first:'9월 24일', second:'9월 25일'},
    ]
    const [time, setTime] = useState('시간을 선택하세요')
    const exhibit_time = [
        {day: '날짜를 선택하세요'},
        {day: '9월 3일', first:'15시', second:'17시', third: '19시'},
        {day: '9월 4일', first:'15시', second:'17시', third: '19시'},
        {day: '9월 17일', first:'14시', second:'16시', third: '18시', fourth:'20시'},
        {day: '9월 18일', first:'14시', second:'16시', third: '18시', fourth:'20시'},
        {day: '9월 24일', first:'14시', second:'15시 30분', third: '17시', fourth:'18시 30분', fifth: '20시'},
        {day: '9월 25일', first:'14시', second:'15시 30분', third: '17시', fourth:'18시 30분', fifth: '20시'},
    ]

    return ( 
    <Div>
        <SelectExhibit exhibit={exhibit} onChangeNumber = {(number1) => {
            setExhibit(number1)
            setDay('날짜를 선택하세요')
            setTime('시간을 선택하세요')
        }}></SelectExhibit>
        <SelectDay exhibit={exhibit} day={day} exhibit_day={exhibit_day} onChangeNumber = {(number2) => {
            setDay(number2)
            setTime('시간을 선택하세요')
        }}></SelectDay>
        <SelectTime exhibit={exhibit} day={day} time={time} exhibit_time={exhibit_time} onChangeNumber = {(number3) => {setTime(number3)}}></SelectTime>
        <Button>관람 신청하기</Button>
    </Div>
    );
}

export default Reservation;