function Content2() {

    useEffect(() => {
        AOS.init();
    }, []);    

    return (
        <>
            <Div><Mtitle>지금부터 당신이 가질 수 있는 궁금증들을 해결해 볼게요.</Mtitle></Div>
            <Div>
                <Btitle>어떤 프로젝트팀인가요 ?</Btitle>
                <Text>누구나의 마음 속 예술을 움 틔우다.'</Text>
                <Text>우리는 ‘프로젝트 움트’ 입니다. '</Text>
                <Text>누구나 예술을 느끼고, 사랑할 수 있다고 생각해요.</Text>
                <Text>누구나 예술가가 될 수도 있구요.</Text>
                <Text>그러한 일들을 만들어 가고 있어요.</Text>
                <Text>관객에게 질문하고, 관객에게 다가가고, 예술가와 관객의 경계가 없는 그런 일이요.</Text>
                <Text>움트라는 프로젝트는, ‘관객 친화적이다’ 라는 생각을 갖도록 만들고 싶어요.</Text>
            </Div>
            <Div>
                <Btitle>왜? 공연이 아닌, 전시인가요?</Btitle>
                <Text>결국, 당신에게 더 가까운 예술을 만들어보고 싶어요.</Text>
                <Text>무용에 대한 인식, 크게는 '예술의 방식에 대한 상상' </Text>
                <Text>그 생각을 다르게 가져보고 싶어서요.</Text>
                <Text>"당신은 무용공연을 본 경험이 있나요?"</Text>
                <Text>"당신은 전시를 본 경험이 있나요?"</Text>
            </Div>
            <Div>
                <Btitle>생 전시는 어떤 프로젝트인가요?</Btitle>
                <Text>말 그대로 전시품이 아닌 生(살 생) 전시, 살아있는 인간을 전시해요.</Text>
                <Text>생 전시 프로젝트는, 무용예술의 다양한 문제점을 파악하여,</Text>
                <Text>보다 더 활성화를 시키기 위한 방안들을 실험하는 ‘연구 프로젝트’ 예요.</Text>
                <Text>프로젝트 움트의 꿈이었던, 다양한 장르와의 Collaboration 을 실현할 수 있게된 감사한 첫 발걸음이에요.</Text>
                <Text>미술 장르의 Main Collaboration Artist_ Yuki 작가님과 함께,</Text>
                <Text>무용수의 Personal Color 를 주제로 전시 작업을 진행했어요.</Text>
                <Text>무용수는 자신의 춤과 이야기를 통해 자신의 색을 - ,</Text>
                <Text>작가님은 무용수의 본연의 색을 느껴, 다양한 Color 작품을 입혔어요.</Text>
            </Div>
            <Div>
                <Text>더불어, 이번 프로젝트는 상업성 연계 실험으로써,</Text>
                <Text>컨셉별 Collaboration 작업을 녹여낼 수 있게 되었답니다.</Text>
                <Text>‘조향, 작곡, 의류 디자이너 브랜드 , 패션 디렉팅 -..'</Text>
                <Text>모두가 움트의 뜻과 바램을 응원해주셨고,</Text>
                <Text>소중하고 좋은 뜻을 가지고, 한 마음으로 열심히 준비하고 있어요.</Text>
                <Text>꿈만 같은 일들을 함께해주심에 감사하며,</Text>
                <Text>결국, 모두에게 뜻 깊은 일이 될 수 있도록 나아가려합니다.</Text>
                <Text>* 해당 프로젝트는 무용수의 직업전환이 아닌,</Text>
                <Text>전공을 통한 생계유지를 위한 기회를 만들어보기 위해</Text>
                <Text>무용수를 브랜딩하기 위한 실험 프로젝트입니다.</Text>
            </Div>
            <Div>&lt;지난 프로젝트 영상&gt;</Div>
            <Div><iframe width="560" height="315" src="https://www.youtube.com/embed/8DUKvaDyd1Y" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe></Div>
        </>
    )
}

export default Content2;